*{
    box-sizing: border-box;
  }
  .normal-display {
    display: block;
  }
  .fugitive-display {
    display:none;
  }
  :root {
    --font-color: rgb(36, 34, 68);
    --font-on-dark: white;
    --scale: scale(1.4);
  }
  .wedding-card {
    margin: 0 !important;
    padding: 0 !important;
  }
  .centerBox{
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    opacity: 1;
  }
  .bgimg {
    z-index: -1;
    left: 0; 
    right: 0;
    margin-left: auto; 
    margin-right: auto;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
  }
  .active {
    filter: blur(10px) opacity(20%) contrast(100%);
  }

  .categoryWrapper{
    overflow: hidden;
    position: absolute;
    width: 100%;
    height:100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .categoryWrapper:after{
    top:0;
    left: 0;
    right:0;
    bottom: 0;
  }
  .categoryWrapper:hover:after{
    opacity: 0.4;
  }

  /* WRITING */
  .categoryWrapper h5{
    width: 100%;
    text-align: center;
    font-size: 25px;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
    color: var(--font-on-dark);
    background:rgba(0, 0, 0, 0.356);
  }
  .categoryWrapper:hover h5{
    transform: translateY(-20px);
    color: var(--font-color);
    background: none;
  }

  /* SUR MAC */
  .raclette-content {
    position: absolute;
    font-size: 12px;
    width: 80%;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
    padding: 10px;
  }
  .raclette-hide {
    opacity: 0;
  }
  .raclette-show {
    opacity: 1;
  }
  .date-title {
    font-size: 15px;
    -webkit-transition: all 2s;
    -moz-transition: all 2s;
    -o-transition: all 2s;
    transition: all 2s;
  }
  .normal-text {
    font-size: 12px;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
  }
  .location-text {
    color: orange;
    font-size: 13px;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
  }
  .rmoveup {
    margin-bottom: 400px;
  }
  .rmovedown {
    margin-top: 300px !important;
  }
  /* FIN MAC TEST */


  /* BUTTON */

  .categoryWrapper button{
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
    position: absolute;
    transform: translatey(60px);
    -webkit-appearance: none;
    border: none;
    background: none;
    color: var(--font-color);
    width: 250px;
    height:50px;
    font-size: 20px;
    padding: 0;
    margin: 0;
    outline: none;
    z-index: 10;
    color: var(--font-on-dark)
  }
  
  .categoryWrapper button span{
    display: block;
    line-height: 50px;
    height: 50px;
    cursor: pointer;
  }
  
  .categoryWrapper button > span:after{
    content:'';
    position: absolute;
    top:0;
    left: 50%;
    width: 20px;
    height: 0;
    
    border: 1px solid var(--font-color);
    border-left: none;
    border-bottom: none;
    
        transition: height 0.15s ease-out, width 0.15s ease-out 0.15s;
  }
  
  .categoryWrapper:hover button > span:after{
    width: calc(50% - 1px);
    height: calc(100% - 2px);
    transition: width 0.15s ease-out, height 0.15s ease-out 0.15s;
  }
  
  .categoryWrapper button > span:before{
    content:'';
    position: absolute;
    top:0;
    right: 50%;
    width: 20px;
    height: 0;
    
    border: 1px solid var(--font-color);
    border-right: none;
    border-bottom: none;
    
        transition: height 0.15s ease-out, width 0.15s ease-out 0.15s;
  }
  
  .categoryWrapper:hover button > span:before{
    width: calc(50% - 1px);
    height: calc(100% - 2px);
    transition: width 0.15s ease-out, height 0.15s ease-out 0.15s;
  }
  
  .categoryWrapper button > span > span:before{
    content:'';
    position: absolute;
    bottom:0;
    right: 0%;
    width: 1px;
    height: 1px;
    opacity: 0;
    
  }
  
  .categoryWrapper:hover button > span > span:before{
    opacity: 1;
    border-bottom: 1px solid var(--font-color);
    width: calc(50%);
    height: 1px;
    transition: opacity 0s ease-out 0.29s, width 0.15s ease-out 0.3s;
  }
  
  .categoryWrapper button > span > span:after{
    content:'';
    position: absolute;
    bottom:0;
    left: 0%;
    width: 1px;
    height: 1px;
    opacity: 0;
    
  }
  
  .categoryWrapper:hover button > span > span:after{
    opacity: 1;
    border-bottom: 1px solid var(--font-color);
    width: calc(50%);
    height: 1px;
    transition: opacity 0s ease-out 0.29s, width 0.15s ease-out 0.3s;
  }
  
  .categoryWrapper button > span > span > span{
    transition: color 0.15s ease-out 0.3s;
    color: transparent;
  }
  
  .categoryWrapper:hover button > span > span > span{
    color:var(--font-color);
  }
  
  .categoryWrapper button > span > span > span:after{
    position: absolute;
    top:0;left:0;right:0;bottom:0;
    color:#1f2e4d;
    content: attr(data-attr-span);
    width: 0%;
    height: 100%;
    background:var(--font-color);
    white-space: nowrap;
    text-align: center;
    margin: auto;
    overflow: hidden;
    display: flex;
    justify-content: center;
    transition: width 0.2s;
  }
  
  .categoryWrapper button:hover > span > span > span:after{
    width: 100%;
    color:var(--font-on-dark);
  }
  /* DESKTOP  */
@media (min-width: 992px) {
  .col-sm {
    height: 700px !important;
    overflow: hidden;
  }
  .categoryWrapper {
    height: 700px !important;
  }
  .bgimg {
    height: 700px !important;
    transform: scale(1.4);
  }
}
  /* MOBILE  */
@media (max-width: 600px) {
  .col-sm {
    height: 200px !important;
  }
  .categoryWrapper {
    height: 200px !important;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
  }
  .bgimg {
    height: 200px !important;
    transform: scale(2);
  }
  .categoryWrapper h5{
    font-size: 18px;
  }
  .make-disappear {
    transform: translateY(-600px)
  }
  .make-bigger {
    height: 1200px;
  }
}
/* IPAD */
@media (max-width: 991px) and (min-width: 600px) {
  .col-sm {
    height: 300px !important;
    min-width: 600px;
  }
  .categoryWrapper {
    height: 300px !important;
  }
  .bgimg {
    height: 300px !important;
    transform: scale(2.1);
  }
  .categoryWrapper h5{
    font-size: 18px;
  }
}
/* MACBOOK SCREEN  */
@media (max-height: 780px) and (min-width: 992px) {
  .bgimg {
    transform: scale(1.2) !important;
    height: 550px !important;
  }
  .col-sm {
    height: 550px !important;
  }
  .categoryWrapper {
    height: 550px !important;
  }
}