:root {
  --blue-font-color: rgb(49, 93, 150);
  --inputfx: translateX(30px)
}

/* BUTTONS AND FX */
.radio-group {
  width: 100% !important;
  margin-left: 35%;
  justify-content: center;
}
.radio-button {
  text-align: left;
  display: block;
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  font-size: 13px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  height: 30px;
}
.radio-button input {
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
/* On mouse-over, add a grey background color */
.radio-button:hover input ~ .radio-form {
  background-color: #ccc;
  border: solid 1px rgb(255, 255, 255);
}

/* When the radio button is checked, add a blue background */
.radio-button input:checked ~ .radio-form {
  background-color: rgb(121, 152, 255);
  border: solid 1px rgb(255, 255, 255);
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.radio-form:after {
  border: solid 1px rgb(255, 255, 255);
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.radio-button input:checked ~ .radio-form:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.radio-button .radio-form:after {
  left: 10px;
  top: 5px;
  width: 5px;
  height: 12px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.radio-form {
  border: solid 1px rgb(255, 255, 255);
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: rgb(192, 192, 192);
  border-radius: 50%;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.51s;
  transition: all 0.5s;
}

/* FORM VALIDATION MESSAGE */
.span-text {
  margin-bottom: 20px;
  margin-top: 10px;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.51s;
  transition: all 0.5s;
}
.active {
  color: black !important;
}
.contact-frame {
  border-bottom: solid 1px white;
  border-top: solid 1px white;
  opacity: 1;
  background: rgb(255, 255, 255, 0.1);
  cursor: pointer;
}
/* GUEST FORM INPUT */
input {
  width: 150px;
  outline-width: 0;
  padding: 5px;
  border: transparent;
  color: var(--blue-font-color);
  font-size: 9px;
  border-radius: 5px;
  background: rgb(231, 240, 252);
  -ms-transform: scale(1.4);
  -moz-transform: scale(1.4);
  -webkit-transform: scale(1.4);
  -o-transform: scale(1.4);
  transform: scale(1.4);
  -webkit-transition: all 1s;
  -moz-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}
input::placeholder {
  color: rgb(0, 0, 0);
  -webkit-transition: all 1s;
  -moz-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}
input:focus::placeholder {
  color: rgb(109, 109, 109);
  
}
.email:focus::placeholder, .name:focus::placeholder {
  -ms-transform: var(--inputfx);
  -moz-transform: var(--inputfx);
  -webkit-transform: var(--inputfx);
  -o-transform: var(--inputfx);
  transform: var(--inputfx);
}
input:hover,
input:active {
  width: 150px;
  outline-width: 0;
  padding: 5px;
  border-radius: 5px;
  color: var(--blue-font-color);
  background: rgb(233, 233, 255);
  -ms-transform: scale(1.4);
  -moz-transform: scale(1.4);
  -webkit-transform: scale(1.4);
  -o-transform: scale(1.4);
  transform: scale(1.4);
}
input:focus {
  width: 150px;
  outline: transparent;
  outline-width: 0;
  background: rgb(232, 237, 255) !important;
  color: black;
  padding: 5px;
  border: none;
  border-radius: 5px;
  background: ghostwhite;
}
.errormsg {
  color: red;
  font-size: 15px;
  transform: translateY(-30px);
}
/* MOBILE  */
@media (max-width: 600px) {
  .errormsg {
    color: red;
    font-size: 12px;
    transform: translateY(-30px);
  }
  
}