:root {
  --font15: "NTR", sans-serif;
  --font16: "Syncopate", sans-serif;
  --blue-font-color: rgb(49, 93, 150);
  --general-font-size: 20px;
  --font-color-dbg: white;
  --font-color-hover: rgb(184, 190, 199);
  --title-card-font-size: 18px;
  --fcolor-text-prog: rgb(49, 93, 150);
  --fcolor-text-prog-hover: rgb(144, 141, 160);
  --fcolor-rsvp: rgb(58, 58, 58);
  /* 
  questrial, red hat display, orbitron, jura */
}
html,
body {
  font-family: var(--font16);
  font-size: var(--general-font-size);
  background: white;
}
hr {
  border: solid 0.1em white;
}
hr.side {
  border: solid 0.1em rgb(126, 126, 126);
  border-width: 1px 0 0;
  clear: both;
  margin: 22px 0 21px;
  height: 0;
}
.App {
  height: 100%;
}
.go-to-programme-link {
  cursor: pointer;
  color: var(--fcolor-text-prog);
}
.go-to-programme-link:hover {
  color: var(--fcolor-text-prog-hover);
}
/* CONTAINER MAIN */
.container-fluid {
  padding: 0 !important;
}
.mobile-screen-only {
  display: none;
}

/* ORIGINAL
.mainbgimg {
  object-fit: cover;
  max-width: 100%;
  min-height: 800px;
} */

/* parallax */
.mainbgimg {
  object-fit: cover;
  width: 100%;
  min-height: 800px;
}

/* CONTAINER INCLUDING CONTAINERS EXCLUDING FOOTER */
.main-container {
  width: 100% !important;
  padding: 0 !important;
  overflow: hidden;
}

/* BUFFER */
.buffer-container {
  text-align: center;
  background: rgb(255, 255, 255);
  padding-top: 50px;
  padding-bottom: 50px;
  padding-left: 5%;
  padding-right: 5%;
  margin: auto;
}
/* TIMER */
.timer-row {
  text-align: center !important;
  max-width: 400px !important;
  margin: auto !important;
  text-align: center;
}
.counter-col {
  margin-top: 3px;
  margin-left: 3px;
  border-radius: 5px;
  /*border: solid 0.1rem rgba(97, 97, 97, 0.275);*/
  color: var(--blue-font-color) !important;
  text-align: center;
  background: rgba(255, 255, 255, 0.446);
}
.datetimer {
  font-size: 18px;
  font-weight: bolder;
}
.datetimerdesc {
  font-size: 14px;
}

/* 
FOOTER */
.contact-link {
  background: rgba(0, 0, 0, 0.514);
}
.footer-container {
  background: rgb(255, 255, 255);
  border: none;
  color: black;
  height: 50px;
}
.footer-info {
  padding-top: 80px;
  margin: auto;
  display: flex;
  justify-content: space-between;
}

/* BUTTONS FOR MAPS AND FORM */

.btnrsvp {
  border: var(--blue-font-color) solid 0.1em;
  font-size: 13px;
  border-radius: 7%;
  color: var(--blue-font-color);
  background: white;
  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;
  transition: 0.8s;
}
.btnrsvp:hover {
  border: solid 0.1em transparent;
  color: rgb(255, 255, 255);
  background: var(--blue-font-color);
}
.btnrsvp:focus {
  border: solid 0.1em;
  color: rgb(49, 49, 49);
  background: transparent;
}
.btnrsvp:active {
  border: white solid 0.1em;
  color: black;
  background: rgb(121, 152, 255);
}
/* TEST CSS */
.use-border {
  border: solid 1px black;
}

/* 
ADDRESS UNDER MAP */
.address {
  font-size: 17px;
  text-align: center;
}
.notvisible {
  display: none;
}
.contact-link:hover {
  color: white;
}
.arrowdiv {
  -webkit-transition: all 1s;
  -moz-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}
.arrowfx {
  transform: translateY(30px);
}
.categoryWrapper i {
  color: rgb(253, 150, 94);
}
.link-hotel a {
  color: var(--blue-font-color);
  cursor:pointer;
  -webkit-transition: all 1s;
  -moz-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}
.link-hotel:hover a {
  color: rgb(36, 26, 92);
}
.contact-frame:hover {
  transform: scale(1.1);
}
.logistic-padding {
  text-align: left;
  padding:25px;
}

/* MOBILE  */
@media (max-width: 600px) {
