    text-align: center;
  }
  .rsvp-carousel {
    border-top: 1px solid var(--fcolor-rsvp);
    border-bottom: 1px solid var(--fcolor-rsvp);
    font-size: 14px;
    padding: 10px;
    color: var(--fcolor-rsvp);
