    margin: 0 auto;
    text-align: center !important;
    background: rgba(255, 255, 255);
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
  }
  .benetcam {
    font-size: auto;
  }
  .merry-go-round-img {
    height: 520px;
    overflow: hidden;
    object-fit: cover;
  }
  .merry-go-round-img-scroll {
    position: sticky;
    height: 300px;
    object-fit: cover;
  }
  .merry-go-round-contact-img {
    height: 320px;
    overflow: hidden;
    object-fit: cover;
  }
  .contact-frame {
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
    overflow: hidden;
  }
  /* CAROUSEL LEGENDS */

  .contact-oncarousel {
    font-size: 12px;
    margin-top: 150px !important;
    z-index: 1;
    color: white;
    text-align: center;
    width: 100%;
