  }
  .contact-link {
    font-size: 12px;
    color: white;
    text-align: center;
    width: 100%;
  }
  .space-div {
    margin-bottom: 130px;
  }
  .datetimer {
    font-size: 18px !important;
  }
  .datetimerdesc {
    font-size: 12px !important;
  }
  .go-to-programme-link {
    font-size: 35px !important;
  }
  /* programme */
  .programme-title, .album-title, .logistic-title {
    font-size: 15px;
    margin-top: 25px;
    margin-left: 30% !important;
  }
  .programme, .album, .invit, .logistic, .hotels {
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    width: 100%;
    height: auto;
    padding-top: 24px;
  }
  .input {
    font-size: 8px !important;
  }
  .core-text {
    text-align: left;
    width: 100%;
    display: inline-block;
  }
  /* INPUT RADIO  */
  .radio-group {
    margin-left: 20% !important;
  }
  /* MAPPING */
  .button-prog-map {
    margin-right: 10%;
    margin-left: 10%;
  }
  .map-col {
    margin: 0 auto;
    text-align: center;
  }
  .content {
    width: 100%;
    display: flex;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
  }
  .mapouter {
    width: 100% !important;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
  }
  .gmap_canvas {
    width: 100% !important;
    height: 330px;
    border-radius: 20px;
    opacity: 1;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
  }
  .gmap_canvas:hover {
    opacity: 1;
  }
  /* NAVBAR  */
  
  .container-programme-row {
    height: auto;
    width: 100%;
  }
  .mobile-col {
    min-width: 100%;
    text-align: center;
    margin: 0 auto;
  }
  /* FOOTER  */
  .footer-button {
    font-size: 10px;
  }
}
