/* NAVIGATION */
.mynavbar {
  letter-spacing: 2px;
  background: transparent !important;
  color:var(--font-color-dbg);
  height: 120px;
  width: 100%;
  position:absolute;
  padding-bottom: 20px;
  padding-top: 20px;
  z-index: 120;
}
.nav-main {
  margin-left: 20px;
  margin-right: 30px;
}

/* Link */
a {
  font-size: 12px;
  color: white;
  display: block;
  text-decoration: none;
  position: relative;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
a:hover {
  text-decoration: none;
  color:white;
}
.link {
  cursor: pointer;
}

/* Toggling part */
.supertoggle {
  position: fixed;
  top: 35px;
  left: 40px;
  z-index: 2;
  border: none;
  background: transparent;
  color: white;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.asupertoggle {
  font-size: 35px;
  font-weight: lighter;
  text-decoration: none;
  color: #272727;
  cursor: pointer;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.asupertoggle:hover {
  color: #bababa;
}
.toggle {
  position: fixed;
  top: 40px;
  left: 40px;
  z-index: 2;
  border: none;
  background: transparent;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.atoggle {
  font-size: 30px;
  text-decoration: none;
  color: #242424;
  cursor: pointer;
}
.atoggle:hover {
  color: #242424;
}
.supermenu {
  margin: 0;
  padding: 0;
  position: fixed;
  top: 0;
  left: 100%;
  width: 100%;
  height: 100%;
  background: rgb(238, 238, 238);
  z-index: 1;
  transition: 0s;
  
}
.superul {
  width: 50%;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  -webkit-transition: all 1s;
  -moz-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}
.superli {
  list-style: none;
}

.superli a {
  font-size: 17px;
  padding: 10px;
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  color: #262626;
  -webkit-transition: all 1s;
  -moz-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}
.superli a:hover {
  background: transparent;
}
.supermenu.superactive {
  left: 0;
  overflow: auto;
}
hr.sidemenu {
  border: solid 0.1em rgb(126, 126, 126);
  border-width: 1px 0 0;
  clear: both;
  margin: 22px 0 21px;
  height: 0;
}


/* MOBILE  */
@media (max-width: 600px) {
  .superul {
    width: 100% !important;
  }
  .superli a {
    font-size: 15px !important;
  }
  .hashtag {
    display: none;
  }
  .ipad-mobile-render {
    display: none;
  }
  .logo-toggler {
    margin: 0 auto;
    text-align: center;
    width: 33%;
  }
  .logo-couple {
    margin: 0 auto;
    text-align: center;
    width: 33%;
  }
  .insta {
    text-align: center;
    width: 33%;
  }
  .insta-logo {
    width: 20px;
  }
  .insta-large-screen {
    display: none;
  }
}

/* DESKTOP  */
@media (min-width: 992px) {
    /* NAVBAR */
    .insta {
      text-align: center;
    }
    .hashtag {
      font-size: 8px;
    }
    .not-visible-for-desktop {
      display: none;
    }
    .logo-couple {
      display: none;
    }
    .insta-logo {
      display: none;
    }
    .insta-large-screen {
      width: 35px;
    }
}
/* IPAD */
@media (max-width: 991px) and (min-width: 600px) {
  .logo-toggler {
    text-align: center;
    width: 20%;
  }
  .logo-couple {
    text-align: center;
    margin: 0 auto;
    width: 60%;
  }
  .insta {
    text-align: center;
    width: 20%;
  }
  .insta-logo {
    width: 25px;
  }
  .insta-large-screen {
    display: none;
  }
  .hashtag {
    display: none;
  }
  .ipad-mobile-render {
    display: none;
  }
}
/* MACBOOK SCREEN  */
@media (max-height: 780px) and (min-width: 992px) {
  .main-logo {
    width: 95px;
  }
}
/* OLD PHONES */
@media (max-width: 321px) {
  .main-logo {
    width: 95px;
  }
  .logo-couple {
    justify-content: center;
    margin: 0 auto;
    text-align: center;
    display: flex;
  }
  .insta-logo {
    width: 25px;
  }
  .insta-large-screen {
    display: none;
  }
}