
/* MOBILE  */
@media (max-width: 600px) {
    :root {
        --height: 710px;
    }
    .lg-screen-only {
        display: none;
    }
    .text-oncarousel {
        font-size: 20px;
        margin-top: 170px !important;
        z-index: 3;
        color: white;
        text-align: center;
        width: 100%;
        position: absolute;
      }
    .div-for-parallax {
        height: var(--height);
        justify-content: center;
        overflow: hidden;
        margin: auto !important;
        max-width: 100%;
        z-index: -1;
    }
    .img-prop {
        height: var(--height);
        display: flex;
        z-index: -1;
        transform: translateX(-200px);
        margin-left: auto;
        margin-right: auto;
    }
    .space-header-timer {
        margin-top: 240px;
    }
    .space-timer-arrow {
        margin-top: 20px;
    }
    
}

/* DESKTOP  */
@media (min-width: 992px) {
    :root {
        --height-desktop: 1600px;
    }
    .paracontainer {
        top: 0;
        height: var(--height-desktop) !important;
    }
    .div-for-parallax {
        height: var(--height-desktop);
        margin-top: -205px; 
        /* 225 */
    }
    .mainparaimg {
        width: 100%;
        height: auto;
        position: absolute;
        z-index: 0;
    }
    .react-parallax {
        height: var(--height-desktop) !important;
        margin-top: -259px;
        /* 180 */
    }
    .style-flanc1 {
        width: 100%;
        position: absolute;
        margin-top: 398px;
        z-index: 2;
        transform: translateY(0px);
    }
    .style-flanc2 {
        width: 100%;
        position: absolute;
        margin-top: 574px;
        z-index: 1;
    }
    .text-oncarousel {
        margin-top: 650px !important;
        z-index: 10;
    }
}
